.HowItWorksDemonstration {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-end;
  background-image: url(/assets/img/demoBackground.jpg);
  width: 520px;
  height: 378px;
  margin-top: -42px;
}

.HowItWorksDemonstration svg {
  overflow: hidden;
}

.HowItWorksDemonstration .phoneDisplay {
  position: absolute;
  width: 193px;
  height: 288px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: stretch;
  justify-content: flex-start;
}

.HowItWorksDemonstration .phoneDisplay .nav {
  display: flex;
  flex-direction: row;
  background-color: #05BEC0;
  width: 99.5%;
  height: 36px;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.HowItWorksDemonstration .phoneDisplay .nav > a {
  pointer-events: none;
}

.HowItWorksDemonstration .phoneDisplay .nav img {
  width: 65px;
}

.HowItWorksDemonstration .phoneDisplay .nav :global(.burger) {
  margin-left: 11px;
  font-size: 7px;
}


@media (max-width: 767px) {
  .HowItWorksDemonstration {
    display: none;
  }
}
