.WhenToBuySquare {
  background-color: white;
  width: 100%;
  min-height: 485px;
  padding-top: 42px;
  padding-right: 47px;
  padding-left: 47px;
  padding-bottom: 42px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
}

.WhenToBuySquare .imgBox {
  background-image: url(/assets/img/whenToBuySquare.jpg);
  width: 520px;
  height: 379px;
  margin-left: 25px;
}

.content {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: stretch;
  max-width: 495px;
}

.content .line {
  margin-bottom: 3px;
}


@media (max-width: 767px) {
  .WhenToBuySquare .imgBox {
    display: none;
  }
  .content {
    max-width: 100%;
    width: 100%;
  }
}
